.navigation__toggle {
  position: absolute;
  display: block;
  width: 21px;
  height: 20px;
  background-color: #ffffff;
  top: 29px;
  right: 30px;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .navigation--closed .navigation__list {
    display: none;
  }
}
.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 3px;
  right: 1px;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 6px 0 0 #231f20, 0 12px 0 0 #231f20;
}
.navigation--closed .navigation__toggle:hover::before {
  background-color: rgba(35, 31, 32, 0.6);
  box-shadow: 0 6px 0 0 rgba(35, 31, 32, 0.6), 0 12px 0 0 rgba(35, 31, 32, 0.6);
}
.navigation--closed .navigation__toggle:active::before {
  background-color: rgba(35, 31, 32, 0.3);
  box-shadow: 0 6px 0 0 rgba(35, 31, 32, 0.3), 0 12px 0 0 rgba(35, 31, 32, 0.3);
}
.navigation--opened .navigation__toggle::before,
.navigation--opened .navigation__toggle::after {
  content: "";
  position: absolute;
  top: 9.5px;
  right: -3px;
  width: 27px;
  height: 2px;
  background-color: #000000;
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}
.navigation--opened .navigation__toggle:hover::before,
.navigation--opened .navigation__toggle:hover::after {
  background-color: rgba(0, 0, 0, 0.6);
}
.navigation--opened .navigation__toggle:active::before,
.navigation--opened .navigation__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
